/**
 * How-To page.
 */
import React from "react"
//import { Link as GatsbyLink } from "gatsby"


/**
 * Page Meta.
 */
const pageMeta = {
  title: 'How-To',
  description: 'How-To stuf...',
}

/**
 * React component (JSX).
 */
const HowToPage = () => (
  <>
  <div className="content">
    <h1>{pageMeta.title}</h1>
    <p>Welcome to the How-To stuff page...</p>
  </div>
  </>
)

HowToPage.pageMeta = pageMeta 

export default HowToPage
